.banner {
    position: relative;
    background-image: url('../images/banner.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    @include flexBox($justifyContent: center);

    &::before {
        content: '';
        position: absolute;
        background-color: rgba(0, 0, 0, 0.2);
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
    }

    &__wrapper {
        position: relative;
        padding-top: $padding-extra-large;
        padding-bottom: $padding-extra-large;
        max-width: 600px;
    }

    &__heading,
    &__subheading {
        color: #fff;
        text-align: center;
        font-weight: bold;
    }

    &__heading {
        font-size: 32px;
        margin: 0 0 16px;

        @media (min-width: $breakpoint-tablet) {
            font-size: 40px;
        }
    }

    &__subheading {
        font-size: 22px;
        margin: 0 0 16px;

        @media (min-width: $breakpoint-tablet) {
            font-size: 24px;
        }
    }

    &__action {
        margin-top: 16px;
        @include flexBox($justifyContent: center);
    }

    &__button {
        // color: $primary-ligth-color;
        // padding: 13px 65px 12px 66px;
        // background-color: #ff0023;
        // border: 1px solid #ff0023;

        // font-family: 'Muli', Sans-serif;
        font-size: 14px;
        font-weight: 600;
        line-height: 1em;
        letter-spacing: 0.15px;
        fill: #ffffff;
        color: #ffffff;
        background-color: #e63137;
        border-style: solid;
        border-width: 1px 1px 1px 1px;
        border-color: #e63137;
        border-radius: 30px 30px 30px 30px;
    }
}
