@import './partials/boilerplate';
@import './partials/shared';

// components
@import './partials/close-button';
@import './partials/section';
@import './partials/column';
@import './partials/header';
@import './partials/navbar';
@import './partials/menu';
@import './partials/form';
@import './partials/card';
@import './partials/info-item';
@import './partials/banner';
@import './partials/feature';
@import './partials/belt';
@import './partials/autorization';
@import './partials/footer';
@import './partials/embed';
@import './partials/utils';

.logo {
    &--menu-fixed {
        @include flexBox($alignItems: center);
        padding-left: $padding-small;

        .logo__image {
            max-width: 64px;
            transition: all 0.5s ease;
        }
    }

    &--hidden {
        .logo__image {
            width: 0;
        }
    }

    &__redirect {
        display: contents;
        text-decoration: none;
    }

    &__image {
        max-width: 132px;

        @media (min-width: $breakpoint-tablet) {
            max-width: 244px;
        }
    }
}

.button {
    color: $primary-ligth-color;
    font-size: 16px;
    font-weight: 600;
    background-color: $primary-color;
    border-width: 1px;
    padding: $padding-small;
    border: 1px solid $primary-color;
    cursor: pointer;

    &__wrapper {
        @include flexBox($alignItems: center);

        & :not(:only-child) {
            margin-left: $padding-extra-small;
            margin-right: $padding-extra-small;

            &:first-child {
                margin-left: 0;
            }
            &:last-child {
                margin-right: 0;
            }
        }
    }

    &--uppercase {
        text-transform: uppercase;
    }

    &--outline {
        color: $primary-color;
        background-color: $primary-ligth-color;
        &-whatsapp {
            color: $action-wp-color;
            background-color: $action-wp-color;
        }
    }

    &--whatsapp {
        background-color: $action-wp-color;
        border-color: $action-wp-color;
    }

    &--secondary {
        font-weight: 600;
        border: 1px solid #e63137;
        background-color: #e63137;

        border-radius: 30px;
    }
}

.icon {
    display: inline-flex;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    width: 16px;
    height: 16px;
    font-size: 16px;

    &--whatsapp {
        background-image: url('../images/icons/icon-whatsapp.svg');
        &-light {
            background-image: url('../images/icons/icon-whatsapp-light.svg');
        }
    }

    &--phone {
        background-image: url('../images/icons/icon-phone.svg');
    }

    &--hold {
        background-image: url('../images/icons/icon-hold.svg');
    }
}

.float {
    @include flexBox($justifyContent: center, $alignItems: center);
    position: fixed;
    width: 60px;
    height: 60px;
    bottom: 16px;
    right: 16px;
    background-color: #25d366;
    color: #fff;
    border-radius: 50px;
    text-align: center;
    font-size: 42px;
    box-shadow: 2px 2px 3px #999;
    z-index: 99;
}

.handorgel {
    border: none !important;
}

.learning {
    &__detail-items {
        @include flexBoxLayout($justifyContent: space-between);
        padding-top: $padding-small;

        [class*='__icon'] {
            max-width: 24px;
        }

        [class*='__heading'] {
            font-size: 16px;
        }
    }
}

.advantage {
    &__heading {
        font-size: 40px;
        margin: 0;
    }

    &__item {
        font-size: 22px;
        line-height: 30px;

        &--accordion {
            font-size: 16px;
            line-height: 24px;
        }
    }
}

.certificate {
    @include flexBox($justifyContent: center);
    [class*='info-item'] {
        max-width: 400px;
    }

    [class*='__image'] {
        height: 86px;
    }

    [class*='__heading'] {
        font-size: 40px;
    }

    [class*='__subheading'] {
        font-size: 16px;
    }
}

.contact {
    .banner {
        background-image: url('../images/banner.png');
        &::before {
            background-color: rgba(0, 0, 0, 0.6);
        }
        [class*='__wrapper'] {
            max-width: initial;
        }

        [class*='__subheading'] {
            font-size: 16px;
            font-weight: 400;
        }
    }
}
