.menu {
    @include flexBox($flexDirection: column);
    position: fixed;
    top: 0;
    left: -200px;
    height: 100vh;
    width: 200px;
    transition: all 0.5s ease;
    background-color: $primary-ligth-color;
    z-index: 99;

    @media (min-width: $breakpoint-tablet) {
        flex: 1;
        @include flexBox($alignItems: center);
        position: sticky;
        left: initial;
        width: initial;
        height: initial;
        box-shadow: 0 5px 6px 0 #00000014;
    }

    &--open {
        left: 0px;
        box-shadow: 2px 2px 2px 0px #00000032;

        @media (min-width: $breakpoint-tablet) {
            box-shadow: none;
        }
    }

    &__header {
        flex-basis: auto;
    }

    &__list {
        @include listStyle;
        flex: 1;
        transition: all 2s ease;

        &--sub {
            background-color: $primary-ligth-color;

            @media (min-width: $breakpoint-tablet) {
                position: absolute;
                left: 0;
                top: 100%;
                box-shadow: 2px 2px 2px 0px #00000032;
            }

            &-collapse {
                height: 0;
                width: 0;
                overflow: hidden;
            }
        }
    }

    &__item {
        font-weight: 600;
        position: relative;
        padding: $padding-small;
        border-bottom: 1px dashed $primary-color;
        text-transform: uppercase;

        @media (min-width: $breakpoint-tablet) {
            display: inline-block;
            border-bottom: none;
        }

        &--active {
            color: $primary-ligth-color;
            background-color: $secondary-color;
        }

        &--sub {
            font-size: 14px;
            padding: $padding-extra-small 0;
            border-bottom: none;
            text-transform: none;
            font-weight: 400;

            @media (min-width: $breakpoint-tablet) {
                padding: $padding-small;
            }
        }
    }

    &__link {
        color: inherit;
        text-transform: inherit;
        text-decoration: none;
        white-space: nowrap;
    }

    &__sticky-button {
        position: absolute;
        right: 16px;
        top: 50%;
        transform: translate(0%, -50%);
        box-shadow: 0 5px 6px 0 #00000014;

        [class*='--secondary'] {
            padding-top: $padding-extra-small;
            padding-bottom: $padding-extra-small;
        }
    }
}
