.info-item {
    flex: 1;

    &__header {
        @include flexBox($justifyContent: center, $alignItems: center);
    }

    &__image {
        max-height: 64px;
    }

    &__content {
        text-align: center;
    }

    &__heading {
        font-size: 32px;
        font-weight: bold;
        margin-top: $padding-small;
        margin-bottom: $padding-extra-small;
    }

    &__subheading {
        font-size: 24px;
    }
}
